import { getExclusiveRelease } from '@/request/api/discoverMusic.js';
import RecommendPanel from './RecommendPanel.vue';
export default {
  data() {
    return {
      exclusiveRelease: []
    };
  },

  components: {
    RecommendPanel
  },

  async mounted() {
    let res = await getExclusiveRelease(); // console.log(res);

    this.exclusiveRelease = res.data.result; // console.log(this.exclusiveRelease);
  },

  methods: {
    // 去往 mv播放页
    goVideoPlayer(mvid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          type: 1
        }
      });
    }

  }
};