export default {
  data() {
    return {
      // 初始位置
      x: 630,
      y: 150,
      // 鼠标距离移动窗口左侧和顶部偏移量
      leftOffset: 0,
      topOffset: 0,
      itemLeftOffset: 0,
      itemTopOffset: 0,
      // 调整盒子是否移动
      boxMove: false,
      // 选项是否移动
      itemMove: false,
      // 调整盒子是否显示
      isAdjustBox: 0,
      // 栏目名称
      columnItems: ['推荐歌单', '播客', '独家放送', '最新音乐', '推荐MV']
    };
  },

  methods: {
    // 调整盒子鼠标按下
    boxMousedown(event) {
      this.leftOffset = event.offsetX;
      this.topOffset = event.offsetY;
      this.boxMove = true;
    },

    // 调整盒子鼠标移动
    boxMousemove(event) {
      // 鼠标按下时才触发
      if (!this.boxMove) return;
      this.x = event.clientX - this.leftOffset;
      this.y = event.clientY - this.topOffset;
    },

    // 调整盒子鼠标抬起
    boxMouseup() {
      this.boxMove = false;
    },

    // 关闭调整盒子
    closeBox() {
      this.isAdjustBox = false;
      this.x = 630;
      this.y = 120;
    },

    // 选项鼠标按下
    itemMousedown(event, index) {
      this.itemTopOffset = event.offsetY;
      this.itemMove = true; // console.log(this.$refs.columnItem[index]);

      this.$refs.columnItem[index].style.backgroundColor = '#fff';
      this.$refs.columnItem[index].style.boxShadow = '0px 1px 4px 1px rgb(205, 205, 205)';
    },

    // 选项鼠标移动
    itemMousemove(event) {
      if (!this.itemMove) return;
      this.y = event.clientY - this.itemTopOffset;
    },

    // 选项鼠标抬起
    itemMouseup(index) {
      this.itemMove = false;
      this.$refs.columnItem[index].style.backgroundColor = '';
      this.$refs.columnItem[index].style.boxShadow = '';
    }

  },
  // 计算属性
  computed: {
    // top 与 left 加上 px
    positionAdjustBox() {
      return `left:${this.x}px; top:${this.y}px;`;
    }

  }
};