import { getHotPodcasts } from '@/request/api/discoverMusic.js';
import RecommendPanel from './RecommendPanel.vue';
export default {
  data() {
    return {
      // 热门电台
      djRadios: []
    };
  },

  components: {
    RecommendPanel
  },

  async mounted() {
    let res = await getHotPodcasts(6); // console.log(res);

    this.djRadios = res.data.djRadios; // console.log(this.djRadios);
  },

  methods: {
    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 格式化播放时长
    formatDuration(time) {
      return this.$formatDuration(time);
    }

  }
};