import { getPersonalizedMv } from '@/request/api/discoverMusic.js';
import RecommendPanel from './RecommendPanel.vue';
export default {
  data() {
    return {
      // 四个推荐 MV
      personalizedMv: [],
      // 是否滑出
      slideLeft: 0
    };
  },

  components: {
    RecommendPanel
  },

  async mounted() {
    let res = await getPersonalizedMv(); // console.log(res);

    if (res.data.code == 200) this.personalizedMv = res.data.result;else this.slideLeft = 1;
  },

  methods: {
    // 显示下拉提示
    mvMouseenter(index) {
      this.$refs.tips[index].classList = 'tips'; // 隐藏播放量

      this.$refs.playCount[index].style.opacity = 0;
    },

    // 隐藏下拉提示
    mvMouseleave(index) {
      this.$refs.tips[index].classList = 'tips1'; // 显示播放量

      this.$refs.playCount[index].style.opacity = 1;
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 去往 mv播放页
    goVideoPlayer(mvid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          // 1是 Mv，2是视频
          type: 1
        }
      });
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    }

  }
};