// 导入 eventBus 模块
import bus from '@/components/menu/DiscoverMusic/DiscoverRecommend/eventBus'; // 导入 store 才可以修改里面的属性，若在计算属性中用 mapState 解构的是只读不可修改的 

import store from '@/store/index.js';
import { mapMutations } from 'vuex';
import { getHomePage } from '@/request/api/discoverMusic.js';
export default {
  data() {
    return {
      // 获取首页顶部轮播图
      banners: [],
      // 翻页模块(默认八张图)
      num: ['zero', 'one', 'two', 'three', 'four', 'five', 'six', 'seven'],
      // 先定义一个值作为定时器
      Interval: '',
      // 当前在中间的 banner 的下标，初始下标为 1 即第二个 banner 在中间
      nowIndex: 1,
      // 处于鼠标下的小圆圈下标
      targetIndex: 0,
      // 监听器的存活
      isInterval: 1
    };
  },

  async mounted() {
    // 在  Local Storage(本地存储) 中检查是否有
    let vuelsHomePage = this.$ls.get('getHomePage', 'null'); // console.log(vuelsHomePage);
    // 在本地中，不用重新获取

    if (vuelsHomePage != 'null') this.banners = vuelsHomePage.data.data.blocks[0].extInfo.banners; // 本地已删除，则重新获取

    if (vuelsHomePage == 'null') {
      // 获取首页各个模块信息
      let res = await getHomePage(); // console.log('获取首页 banners', res);
      // 转存到本地，有效期为 10 分钟

      this.$ls.set('getHomePage', res, store.state.validityTime); // 首页接口的第一个 block 就是轮播图模块

      this.banners = res.data.data.blocks[0].extInfo.banners;
    } // console.log('banners', this.banners);
    // 自动翻页函数，把 next() 方法传入


    this.Interval = setInterval(this.next, 1500);
  },

  // 写在 created 阶段（现成的解决方案）
  created() {
    // 接收从 router 路由 index.js DiscoverRecommendList 传过来的清除监听器的标志
    bus.on('isInterval', val => {
      // console.log('接收到 isInterval', val);
      this.isInterval = val;
    });
  },

  methods: {
    // 点击 banner，形参为 banner 对象，targetType = 1 新歌，3000 网页，10 新碟，1006/3000 活动，1004 MV
    clickBanner(item, index) {
      // 点击的是当前在中间显示的 banner
      if (this.$refs.li[index].classList[0] == 'one') {
        console.log(item);

        if (item.targetType === 1) {
          // 播放歌曲
          const songs = []; // 只有一首歌的播放列表

          songs.push(item.song); // 显示底部音乐播放组件

          this.updateIsPlayBox(1); // 更新播放列表

          this.updatePlayList(songs); // 更新歌单名(歌曲来源)

          this.updateSongListName('banner'); // 更新歌单 id(歌曲来源)

          this.updateSongListId(0); // 更新播放列表下标

          this.updatePlayListIndex(0);
        } else if (item.targetType === 3000) {
          // 在新窗口打开该网页
          window.open(item.url);
        } else if (item.targetType === 10) {
          // 去往专辑页
          this.$router.push({
            path: '/SongListDetail',
            query: {
              id: item.targetId,
              // 1歌单 2专辑
              type: 2
            }
          });
        } else if (item.targetType === 1004) {
          // 去往 mv播放页
          this.$router.push({
            path: '/VideoPlayer',
            query: {
              mvid: item.targetId,
              type: 1
            }
          });
        } else {// console.log('其它', item.targetType);
        }
      }
    },

    // 右翻页
    next() {
      // unshift() 把数组的最后一个添加到最前面
      this.num.unshift(this.num[7]); // pop() 删除原数组最后一个

      this.num.pop(); // 利用当前新数组重新给 li 添加类名

      for (var i = 0; i < this.num.length; i++) {
        // setAttribute()把指定属性设置为指定值。若不存在该指定名称的属性，将创建一个新属性。且它是只能通过元素节点对象调用的函数。
        // error: Cannot read properties of undefined (reading 'setAttribute') 无法读取未定义的属性
        this.$refs.li[i].setAttribute('class', this.num[i]); // this.$refs.li[i].classList = this.num[i]
      } // console.log(this.$refs.li);
      // 下一个 banner 移动中间


      this.nowIndex++; // 对中间的 banner 的下标的处理

      this.nowIndexSave(this.nowIndex); // 小圆圈跟着图片变色

      this.circleColor(); // console.log('这时的 num 数组：' + this.num);
    },

    //左翻页
    previous() {
      this.num.push(this.num[0]);
      this.num.shift();

      for (var i = 0; i < this.num.length; i++) {
        this.$refs.li[i].setAttribute('class', this.num[i]);
      }

      this.nowIndex--;
      this.nowIndexSave(this.nowIndex);
      this.circleColor(); // console.log('这时的 num 数组：' + this.num);
    },

    // 鼠标移入清除定时器 停止自动播放，按钮显示
    mouseover() {
      this.btnAppear();
      clearInterval(this.Interval);
    },

    // 鼠标移出恢复定时器 继续自动播放，按钮消失
    mouseout() {
      this.btnDisappear(); // ！最好先清除同名的定时器

      clearInterval(this.Interval); // 再重新恢复

      this.Interval = setInterval(this.next, 1500);
    },

    // 按钮显示
    btnAppear() {
      // 操作 dom：
      // var leftBtn = document.querySelector('.leftBtn');
      // var rightBtn = document.querySelector('.rightBtn');
      // leftBtn.style.display = 'block';
      // rightBtn.style.display = 'block';
      this.$refs.leftBtn.style.display = 'block';
      this.$refs.rightBtn.style.display = 'block';
    },

    // 按钮消失
    btnDisappear() {
      // var leftBtn = document.querySelector('.leftBtn');
      // var rightBtn = document.querySelector('.rightBtn');
      // leftBtn.style.display = 'none';
      // rightBtn.style.display = 'none';
      this.$refs.leftBtn.style.display = 'none';
      this.$refs.rightBtn.style.display = 'none';
    },

    // 对中间的 banner 的下标的处理
    nowIndexSave(index) {
      if (index === 8) {
        this.nowIndex = 0;
      } else if (index === -1) {
        this.nowIndex = 7;
      } else {
        this.nowIndex = index;
      }
    },

    // 小圆圈跟随图片变色
    circleColor() {
      // 排他，先全部灰色
      for (var i = 0; i < this.$refs.list.length; i++) {
        this.$refs.list[i].style.backgroundColor = 'rgb(202, 202, 202)';
      } // 再给对应下标的小圆圈加色


      this.$refs.list[this.nowIndex].style.backgroundColor = '#ec4141';
    },

    // banner 跟随鼠标在小圆圈上的指引
    listMouseover(val) {
      // 鼠标移入小圆圈清除定时器停止自动播放
      clearInterval(this.Interval); // 获取所有小圆圈

      let allList = val.target.parentNode.children; // 获得当前处于鼠标下的小圆圈下标

      this.targetIndex = this.tabIndex(val.target, allList); // 当前在中间的 banner 的下标 与 当前处于鼠标下的小圆圈下标 的差值

      let disparity = this.targetIndex - this.nowIndex; // 调用'差值'次数的 next() 或 previous()

      if (disparity > 0) {
        for (var i = 0; i < disparity; i++) {
          this.next();
        }
      }

      if (disparity < 0) {
        // 注意 disparity 先 Math.abs() 取正
        for (var j = 0; j < Math.abs(disparity); j++) {
          this.previous();
        }
      }
    },

    // 当前元素在父级子元素们的位置
    tabIndex(target, nodeList) {
      for (let i = 0; i < nodeList.length; i++) {
        if (target === nodeList[i]) {
          return i;
        }
      }
    },

    ...mapMutations(['updateIsPlayBox', 'updatePlayList', 'updateSongListName', 'updateSongListId', 'updatePlayListIndex'])
  },
  watch: {
    isInterval() {
      // 清除定时器
      if (this.isInterval === 0) clearInterval(this.Interval);
    }

  }
};