import { getRecommendedSongList } from '@/request/api/discoverMusic.js';
import RecommendPanel from './RecommendPanel.vue';
export default {
  data() {
    return {
      // 推荐歌单
      recommendedSongList: [],
      // 当天日期(号)
      date: 0,
      // 展示的歌单
      showingSongList: []
    };
  },

  components: {
    RecommendPanel
  },

  async mounted() {
    // 获取推荐歌单
    let res = await getRecommendedSongList(9); // console.log('获取推荐歌单', res);

    this.recommendedSongList = res.data.result; // console.log(this.recommendedSongList);
    // 获取当天日期(号)

    this.date = new Date().getDate();
  },

  methods: {
    // 去往每日推荐歌曲页
    goRecommendSongs() {
      this.$router.push('/RecommendSongs');
    },

    // 去往歌单详情页
    goDetail(id) {
      this.$router.push({
        path: '/SongListDetail',
        query: {
          id: id,
          // 1歌单 2专辑
          type: 1
        }
      });
    },

    // 推荐歌单显示播放键
    playKeyMouseenter(index) {
      // 因为经渲染之后 refs 里面有很多个 playKey，所以要指定哪一个(带下标)
      this.$refs.playKey[index].classList = 'playKey';
    },

    // 推荐歌单隐藏播放键
    playKeyMouseleave(index) {
      this.$refs.playKey[index].classList = 'playKey1';
    },

    // 每日歌曲推荐显示播放键及下拉提示
    everydayMouseenter() {
      this.$refs.everyPlayKey.classList = 'playKey';
      this.$refs.tips.classList = 'tips';
    },

    // 每日歌曲推荐隐藏播放键及下拉提示
    everydayMouseleave() {
      this.$refs.everyPlayKey.classList = 'playKey1';
      this.$refs.tips.classList = 'tips1';
    },

    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    }

  }
};