import { getNewSong } from '@/request/api/discoverMusic.js';
import RecommendPanel from './RecommendPanel.vue';
export default {
  data() {
    return {
      newSongs: [],
      // 前四首
      musicGroup1: [],
      // 5-8首
      musicGroup2: [],
      // 9-12首
      musicGroup3: []
    };
  },

  components: {
    RecommendPanel
  },

  async mounted() {
    // 获取新歌速递全部歌曲
    let res = await getNewSong(0); // console.log(res);

    this.newSongs = res.data.data; // console.log(this.newSongs);
    // 分组

    this.musicGroup1 = this.newSongs.slice(0, 4);
    this.musicGroup2 = this.newSongs.slice(4, 8);
    this.musicGroup3 = this.newSongs.slice(8, 12); // console.log(this.musicGroup1);
    // console.log(this.musicGroup2);
    // console.log(this.musicGroup3);
  },

  methods: {
    // 去往 mv播放页
    goVideoPlayer(mvid) {
      this.$router.push({
        path: '/VideoPlayer',
        query: {
          mvid: mvid,
          // 1是 Mv，2是视频
          type: 1
        }
      });
    }

  }
};